.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h1,
p {
  font-family: Lato;
}
.appStyle {
  height: 250px;
  display: flex;
}
.formStyle {
  margin: auto;
  padding: 10px;
  border: 1px solid #C9C9C9;
  border-radius: 5px;
  background: #F5F5F5;
  width: 300px;
  display: block;
}
.labelStyle {
  margin: 10px 0 5px 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 15px;
}
.inputStyle {
  margin: 5px 0 10px 0;
  padding: 5px;
  border: 1px solid #BFBFBF;
  border-radius: 3px;
  box-sizing: border-box;
  width: 100%;
}
.submitStyle {
  margin: 10px 0 0 0;
  padding: 7px 10px;
  border: 1px solid #EFFFFF;
  border-radius: 3px;
  width: 100%;
  font-size: 15px;
  color: white;
  display: block;
}

.button-enabled {
  background: #3085D6
}

.button-disabled {
  background: #cccccc;
  pointer-events: none;
}

.pb-30 {
  padding-bottom: 30px;
}
.mb-20 {
  margin-bottom: 20px;
}
.startDate {
  float: left;
}
.endDate {
  margin-left: 620px;
}
.display-flex {
  display: flex;
}

.text-align {
  text-align: center;
}

.app-container {
    left: 66%;
    width: 30em;
    height: 18em;
    margin-left: 30%;
    margin-top: 100px;
}

.c-red {
  color: red;
}

select {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  outline: 0;
  box-shadow: none;
  border: 0 !important;
  background: #fdfcfc;
  background-image: none;
}
/* Remove IE arrow */
select::-ms-expand {
  display: none;
}
/* Custom Select */
.select {
  position: relative;
  display: flex;
  width: 18.7em;
  height: 2em;
  line-height: 2;
  background: #fdfcfc;
  overflow: hidden;
  border-radius: .25em;
  border: 1px solid #BFBFBF;
}
select {
  flex: 1;
  padding: 0 .5em;
  color:black;
  font-weight: 100;
  cursor: pointer;
}
/* Arrow */
.select::after {
  content: '\25BC';
  position: absolute;
  top: 0;
  right: 0;
  padding: 0 1em;
  background: #7b8080;
  cursor: pointer;
  pointer-events: none;
  -webkit-transition: .25s all ease;
  -o-transition: .25s all ease;
  transition: .25s all ease;
}
/* Transition */
.select:hover::after {
  color: #f39c12;
}
